import { Component } from '@angular/core';
import { 
   NavController, 
   NavParams,
   ModalController,
  ActionSheetController,
  LoadingController,
  Platform,
  ToastController,
  ViewController,
  normalizeURL,
 } from 'ionic-angular';
import { BaseUI } from '../common/baseui';
import {Storage} from '@ionic/storage';
import { RestProvider} from '../../providers/rest/rest';

// 调用摄像头
// npm install --save @ionic-native/file @ionic-native/file-path @ionic-native/transfer   @ionic-native /camera 
// ionic cordova plugin add cordova-plugin-camera --save  相机     @4.3.2
// ionic cordova plugin add cordova-plugin-file --save  本地文件   @  4.3.2
// ionic cordova plugin add cordova-plugin-file-transfer  --save   传输相关文件     3.14.0
// ionic cordova plugin add cordova-plugin-filepath --save   路径相关 @4.3.2
import {File} from '@ionic-native/file';
import {Transfer,TransferObject} from '@ionic-native/transfer';
// TransferObject
import {FilePath} from '@ionic-native/File-path';
import {Camera} from '@ionic-native/camera';

declare var cordova:any;//导入第三方库，

// IonicPage,
/**
 * Generated class for the HeadfacePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

// @IonicPage()
@Component({
  selector: 'page-headface',
  templateUrl: 'headface.html',
})
export class HeadfacePage  extends BaseUI{//继承与BaseUI


  userId:string;
  errorMessage:string;
  lastImage:string=null;// 保存最后一次



  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public viewCtrl:ViewController,
    public actionSheetCtrl:ActionSheetController,
    public storage:Storage,
    public modalCtrl:ModalController,
    public rest:RestProvider,
    public loadingCtrl:LoadingController,
    public camera:Camera,
    public file:File,
    public filePath:FilePath,
    public transfer:Transfer,
    public platform:Platform,
    public toastCtrl:ToastController,


  ) {
    super();
  }


// 每次加载进来，从storage 本地存储把 UserId 拿出来，存储图片的时候用到
  ionViewDidEnter() {// 获取UserId
    this.storage.get('UserId').then((val)=>{
      if(val!=null){  // 如果val 不等与空，那么我们在下边赋值
        this.userId=val;
      }
    });
  }

  presentActionSheet(){
  let actionSheet=this.actionSheetCtrl.create({
    title:'选择图片',
    buttons:[
      {
        text:'从图片库中选择',
        handler:()=>{
        this.takePicture(this.camera.PictureSourceType.PHOTOLIBRARY);
        }
    
    },
      {
        text:'使用相机',
        handler:()=>{
       this.takePicture(this.camera.PictureSourceType.CAMERA);
        }
      },
      {
        text:'取消',
        role:'cancel'
        
      }
    ],
  });
  actionSheet.present();
  }
//这个函数 大，多看几遍  9-5  获取图片
  takePicture(sourceType){
   // 定义相机的一些参数
   var options={
     quality:100, // 拍照质量 100
     sourceType: sourceType,
     saveToPhotoAlbum:false,//是否保存拍摄照片到相册中去
     correctOrientation:true,//是否纠正拍摄的照片的方向

   };
   //获取图片的方法
   this.camera.getPicture(options).then((imagePath)=>{
     //特别处理android 平台的文件路径问题
     if(this.platform.is('android') && sourceType=== this.camera.PictureSourceType.PHOTOLIBRARY){
     this.filePath.resolveNativePath(imagePath)  //获取android下的真实路径
     .then(filePath=>{
       //获取正确的路径
       let correctPath=filePath.substr(0,filePath.lastIndexOf('/')+1);
       //获取正确的文件名
       let currentName=imagePath.substring(imagePath.lastIndexOf('/')+1,imagePath.lastIndexOf('?'));
       this.copyFileToLocalDir(correctPath,currentName,this.createFileName());
     })
     }
     else{
       //获取正确的路径
      let correctPath=imagePath.substr(0,imagePath.lastIndexOf('/')+1);
      //获取正确的文件名
      let currentName=imagePath.substring(0,imagePath.lastIndexOf('/')+1);
      this.copyFileToLocalDir(correctPath,currentName,this.createFileName());
     }
   },(err)=>{
     super.showToast(this.toastCtrl,"选择图片出现错误，请在app中检查权限")
   });
  }

  // 将获取到的图片或者相机拍摄到的图片进行一下另存为，用于后期图片上传的后期使用
  copyFileToLocalDir(namePath,currentName,newFileName){
    this.file.copyFile(namePath,currentName,cordova.file.dataDirectory,newFileName).then(success=>{
      this.lastImage=newFileName;
    },erro=>{
      super.showToast(this.toastCtrl,'存储图片到本地的图库出现错误');
    });
  }

  //为文件生成一个新的文件名
  createFileName(){
    var d=new Date(),// 日期做一个 唯一值取到值
    n=d.getTime(),// 精确的时间
    newFileName=n+".jpg";
    return newFileName;
  }

  // 处理图片的路径为可以上传的路径
   public pathForImage(img){
    if(img === null){
      return '';
    }else{
      return normalizeURL(cordova.file.dataDirectory+img);
      // 此处为官方bug  新版本无次问题
    }
  }

 // ### fileTransfer上传的参数
 uploadImage(){
   var url='https://imoocqa.gugujiankong.com/api/account/uploadheadface';
   var targetPath=this.pathForImage(this.lastImage);
   var filename=this.userId+".jpg";//定义上传的文件名
   var options={
     fileKey:'file',
     fileName:filename,
     chunkedMode:false,
     mimeType:"multipart/form-data",
     params:{'fileName':filename,'userid':this.userId }
   };
   const fileTransfer:TransferObject=this.transfer.create();
   var loading=super.showLoading(this.loadingCtrl,"上传中...");
//开始上传
fileTransfer.upload(targetPath,url,options).then(data=>{
loading.dismiss();
super.showToast(this.toastCtrl,'图片上传成功');
// 用户看清弹窗提示后进行页面的关闭
setTimeout(()=>{
  this.viewCtrl.dismiss();
},3000);
} ,err=>{
  loading.dismiss();
  super.showToast(this.toastCtrl,"图片上传发生从未，请重试.")
});
 }
}



